import React from 'react';
import I from 'immutable';
import { connect } from 'react-redux';

class FirstPage extends React.PureComponent {

  handleSetStore = () => {
    const {
      setCounter, setStoreTest, counter
    } = this.props;

    if (counter % 2 === 0) {
      setStoreTest('close');
    } else {
      setStoreTest('open');
    }
    setCounter(counter + 1);
  }
  render() {
    const {
      setStoreTest, data,
    } = this.props;
    return (
      <div>
        <button onClick={ this.handleSetStore }>test test</button>
        <div>{ data }</div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  const firstPage = state.get('firstPage', I.Map());    
  return {
    data: firstPage.get('data', ''),
    counter: firstPage.get('counter', 0)
  };
};

const mapDispatchToProps = dispatch => ({
  setStoreTest: data => dispatch({ type: 'SET_STORE_TEST', data }),
  setCounter: counter => dispatch({ type: 'SET_STORE_COUNTER', counter })
});

export default connect(mapStateToProps, mapDispatchToProps)(FirstPage);